import React, { memo } from 'react';
import { UserCog, Users } from 'lucide-react';
import InfoSection from '../InfoSection';
import InfoItem from '../InfoItem';
import BooleanValue from '../BooleanValue';

const GestaoProfessores = memo(({ escola }) => {
  if (!escola) return null;

  return (
    <InfoSection title="Gesto e Professores" icon={UserCog}>
      <InfoItem label="Gesto/Nome" value={escola.gestao} />
      <InfoItem label="Outros Funcionrios" value={escola.outros_funcionarios} />
      <InfoItem label="Professores Indgenas" value={escola.professores_indigenas} />
      <InfoItem label="Professores No Indgenas" value={escola.professores_nao_indigenas} />
      <InfoItem 
        label="Professores Falam a Lngua Indgena" 
        value={<BooleanValue value={escola.professores_falam_lingua} />} 
      />
      <InfoItem label="Formao dos Professores" value={escola.formacao_professores} />
      <InfoItem label="Formao Continuada" value={escola.formacao_continuada} />
    </InfoSection>
  );
});

export default GestaoProfessores; 