import React, { memo } from 'react';
import { School } from 'lucide-react';
import InfoSection from '../InfoSection';
import InfoItem from '../InfoItem';
import BooleanValue from '../BooleanValue';

const BasicInfo = memo(({ escola }) => {
  if (!escola) return null;

  return (
    <InfoSection title="Informaes Bsicas" icon={School}>
      <InfoItem label="Nome da Escola" value={escola.titulo} />
      <InfoItem label="Municpio" value={escola.municipio} />
      <InfoItem label="Endereo" value={escola.endereco} />
      <InfoItem label="Terra Indgena" value={escola.terra_indigena} />
      <InfoItem label="Tipo de Escola" value={escola.tipo_escola} />
      <InfoItem 
        label="Parcerias com o Municpio" 
        value={<BooleanValue value={escola.parcerias_municipio} />} 
      />
      <InfoItem label="Diretoria de Ensino" value={escola.diretoria_ensino} />
      <InfoItem label="Ano de Criao" value={escola.ano_criacao} />
    </InfoSection>
  );
});

export default BasicInfo; 