import React, { memo } from 'react';
import { GraduationCap } from 'lucide-react';
import InfoSection from '../InfoSection';
import InfoItem from '../InfoItem';
import BooleanValue from '../BooleanValue';

const Ensino = memo(({ escola }) => {
  if (!escola) return null;

  return (
    <InfoSection title="Ensino" icon={GraduationCap}>
      <InfoItem label="Modalidade de Ensino" value={escola.modalidade_ensino} />
      <InfoItem label="Nmero de Alunos" value={escola.numero_alunos} />
      <InfoItem label="Disciplinas Bilngues" value={escola.disciplinas_bilingues} />
      <InfoItem 
        label="Material Pedaggico No Indgena" 
        value={<BooleanValue value={escola.material_nao_indigena} />} 
      />
      <InfoItem 
        label="Material Pedaggico Indgena" 
        value={<BooleanValue value={escola.material_indigena} />} 
      />
      <InfoItem label="Prticas Pedaggicas Indgenas" value={escola.praticas_pedagogicas} />
      <InfoItem label="Formas de Avaliao" value={escola.formas_avaliacao} />
    </InfoSection>
  );
});

export default Ensino; 